.header {
    height: 60px;
    margin: auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.header__nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    margin: 0 20px;
}

.list {
   list-style: none;
   display: flex;
   flex-direction: row;
   align-items: center;   
}

.header__option {
    padding: 30px;
}



