.sidebar {
    flex: 0.2;
    height: 85vh !important;
    position: relative;
    border-right: 1px solid rgb(182, 173, 173);
}

.searchBar {
    width: 100%;
    color: gray;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.search__input {
    border: none;
    width: 100%;
    font-size: 11px;
}

.sidebar__options {
    margin-bottom: 50%;
}

.sidebar__rule {
    border: none;
    border-bottom: 1px solid rgb(182, 173, 173);
    border-color: gray;
    width: 100%;
}

.footer {
    display: flex;
    bottom: 0;
    position: absolute;
    justify-content: space-between;
    padding-top: 0;
    padding-bottom: 0;
    width: 100%;
    font-size: 12px;
    background-color: rgb(224, 223, 223);
}

.footer__button1 {
    border: 1px solid gray;
    padding: 2px;
    width: 100%;

}
.footer__button2 {
    border: 1px solid gray;
    padding: 2px;
    width: 100%;
}