.main {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}
.home__title {
    margin-top: 45px;
    margin-left: 12px;
    margin-bottom: 40px;
    align-items: center;
    height: 60px;
    width: 30%;
    font-size: 24px;
}

.home__title__nav {
    width: 60%;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 200px;
}

.home__title__option {
    display: flex;
    align-items: center;
    padding: 5px; 
    font-size: 20px;
    color: rgb(30, 118, 190)  
}
